* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4f4f4;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 100%;
  height: 100px;
  background-color: #fff;
  flex-direction: row;
  justify-content: center;
  row-gap: 30px;
  padding: 20px;
  display: flex;
}

.search-form {
  align-items: center;
  display: flex;
}

.input {
  width: 800px;
  height: 50px;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
}

.input::-ms-clear, .input::-ms-reveal {
  display: none;
}

.input::-webkit-search-cancel-button, .input::-webkit-search-clear-button {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: none;
}

button {
  color: #fff;
  height: 50px;
  width: 150px;
  cursor: pointer;
  background-color: #000;
  border: 2px;
  border-radius: 5px;
  margin-left: 10px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  background-color: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
  border-bottom: 1px solid #ccc;
}

.info {
  padding: 10px;
}

.info-item {
  margin-bottom: 8px;
}

.load-more {
  color: #fff;
  height: 50px;
  width: 150px;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 5px;
  margin: 20px;
}

.is-hidden {
  display: none;
}

